 #main{
   margin-top: 8rem;
  
 }

@media screen and (max-width: 768px) {
    html{
        font-size: 82%;
    }
     #home{
        grid-template-columns: repeat(2,1fr);
     }
    #about{
        margin-top: 2rem;
    }
    #project{
      grid-template-columns: repeat(1,1fr);
        margin-left: 2rem;
     }
    
  }
  
  @media screen and (max-width: 480px) {
    #home{
        grid-template-columns: repeat(1,1fr);
        
     }
     .Img{
        margin-left: 5.5rem;
     }
      #about{
        margin-top: 15rem;
    }
    #skill{
        grid-template-columns: repeat(2,1fr);
    }
    #project{
      grid-template-columns: repeat(1,1fr);
      margin-left: 2rem;
     }
    

  }